*{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
body {
    padding: 60px;
}

input {
    width: 100%;
    background-color: #FFFF;
    color: gray;
    height: 1.3rem;
}
.cardGrid {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}
.gif {
    align-content: center;
    border: 1px solid gray;
    border-radius: 10px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    padding: 16px;
}
.card p {
    padding: 2px;
    text-align: center;
    font-size: 17px;
    font-weight: 600;
    font-family: Montserrat, Arial;
}
.card img {
    max-height: 170px;
    border-radius: 4px;
}